<template>
	<div class="top">
		<!-- <el-carousel height="450px" >
	      <el-carousel-item >
	        <img src="../../src/assets/banner.jpg" alt="">
	      </el-carousel-item>
	      <el-carousel-item >
	        <img src="../../src/assets/banner2.jpg" alt="">
	      </el-carousel-item>
	      <el-carousel-item >
	        <img src="../../src/assets/home-bg.jpg" alt="">
	      </el-carousel-item>
	    </el-carousel> -->
		<div class="tit">
			<div class="tips">
				Fatimah.Xu Blog
			</div>		
			<div class="banner pb-20">
				<div class="cf up">X.X &nbspBlog</div>
				<div class="cf ta-c  down">Just do it!</div>
			</div>
		</div>	
		
		<nav>
			<div>
				<router-link  to="/" exact>OVERVIEW</router-link>
				<router-link to="/add" exact>ADDBLOG</router-link>	
			</div>
		</nav>
	</div>	
</template>

<script>
	export default {
		name: 'blog-header',
		data(){
			return{
				itemImg :[]
			}
		}

	}
	
</script>

<style scoped>
	.el-carousel__item h3 {
	color: #475669;
	font-size: 14px;
	opacity: 0.75;
	line-height: 150px;
	margin: 0;
	}

	.el-carousel__item:nth-child(2n) {
	 background-color: #99a9bf;
	}

	.el-carousel__item:nth-child(2n+1) {
	 background-color: #d3dce6;
	}
	nav{
		position: absolute;
		bottom: 0;
		left: 0;		
		padding: 0 10px ;	
		margin-bottom: 5px;	
		display: flex;
		justify-content: space-between;
	}	
	a{
		display: inline-block;
		width: 80px;
		color: #eee;
		font-weight: bold;
		text-decoration: none;
		padding: 10px;
		text-align: center;
	}
	.router-link-active{	
		color: #ccc;
	}
	.top{
		position: relative;
		margin: 0 auto;
	}
	@media screen and (max-width: 768px) {
	  	.tit{
			overflow: hidden;
			height: 300px;
			width: 100%;
			background-attachment: scroll;
			
			background-image:  url("../../src/assets/home-bg.jpg");	
			background-size: 100% 100%;
			-moz-background-size:100% 100%;
		} 
		.tit .up{
			font-size: 36px;
		}
		.tit .down{
			font-size: 16px;
			font-style: italic;
			padding: 5px;
		}
	}
	@media screen and (min-width: 768px) {
	  	.tit{
			overflow: hidden;
			height: 485px;
			width: 100%;
			background-attachment: scroll;
			background-image:  url("../../src/assets/home-bg.jpg");	
			background-size: 100% 100%;
			-moz-background-size:100% 100%;
		} 
		
		.tit .down{
			font-size: 30px;
			padding: 10px;
			font-style: italic;
		}
	}
	/* .tit img{
		max-height: 485px;
		width: 100%;
		min-width: 1208px;
		object-fit: cover;
	} */
	.banner{
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%,-50%);
		z-index: 1;
		font-size: 76px;
		font-weight: bold;
		color: #ffffff;
	}
	.tips{
		position: absolute;
		top: 15px;
		right: 15px;
		z-index: 2;
		color: #ddd;
		font-size: 24px;
		font-weight: bold;
		font-family: Open Sans!important;
		/* font-family: sans-serif; */
		/* Open Sans,Helvetica Neue,Helvetica,Arial */
	}

		
</style>


